<template>
  <div class="r3box">
    <div v-for="item in items">
      <h3>{{item.h}}</h3>
      <p>{{item.p}}</p>
      <div>
        <pre v-highlight>
            <code class="css">{{item.c}}
            </code>                   
        </pre>
      </div>
    </div>
    <comment></comment>
  </div>
</template>

<script>
import Comment from "./comment";
export default {
  name: "pointerEvents",
  components: { Comment },
  data() {
    return {
      items: []
    };
  },
  mounted() {
    axios("/static/css.json").then(res => {
      var a = res.data.data;
      //console.log(a)
      for (var i = 0; i < a.length; i++) {
        a[i].c = unescape(a[i].c);
        a[i].p = a[i].p;
        a[i].h = a[i].h;
      }
      this.items = a;
      //console.log(JSON.stringify(this.items));
    });
    //gitalk
    var gitalk = new Gitalk({
      clientID: `f9b81b4066ab29b976f7`,
      clientSecret: `e656e69ef96dfd7e415f3ff7797576f7c0ba0772`,
      repo: `blog-comment`,
      owner: "pistachio-yaya",
      admin: ["pistachio-yaya"],
      id: window.location
    });
    gitalk.render("gitalk-container");
  }
};
</script>


<style scoped>

</style>




