<template>
  <div class="contactBox">
    <div class="q">
      <div class="qr_code"></div>
    </div>
    <div class="wx">
      <div class="qr_code"></div>
    </div>
    <div class="gotop" v-on:click="gotop" >
      
    </div>
  </div>
</template>

<script>
export default {
  name: "contactBox",
  data(){
      return{
      }
  },
  mounted(){
       window.onscroll = function(){
           console.log(window.scrollY)
           window.scrollY > 700 ? document.querySelector(".gotop").style.visibility = "visible" : document.querySelector(".gotop").style.visibility = "hidden"
       }
  },
  methods:{
      gotop:function(){
          window.scrollTo(0,0);
      },
      scroll:function(){
          
      }
  }
};
</script>


<style scoped>
.contactBox {
  position: fixed;
  right: 10px;
  bottom: 60px;
  display: flex;
  flex-direction: column;
}
.q,
.wx,
.gotop {
  width: 40px;
  height: 40px;
  border: 1px solid #ddd;
  background-image: url("../assets/qqw.png");
}
.q {
  background-position: 0% 0%;
}
.q:hover {
  background-position: 0% 90%;
  background-color: #289bd5;
  border:1px solid #289bd5;
}

.q:hover .qr_code {
  display: block;
  background-image: url("../assets/qq.png");
  background-repeat: no-repeat;
}
.wx {
  height:40px;
  background-position: 98% 0%;
}
.wx:hover {
  background-position: 98% 88%;
  background-color: #00bb00;
  border:1px solid #00bb00;
}
.wx:hover .qr_code {
  display: block;
  background-image: url("../assets/weixin.png");
}
.gotop {
  background-position: 49% 0%;
  visibility:hidden;
}
.gotop:hover {
  background-position: 48% 88%;
  background-color: #787878;
  border:1px solid #787878;

}

.qr_code {
  position: fixed;
  right: 70px;
  bottom: 100px;
  width: 100px;
  height: 100px;
  border: 3px solid #e2e3e5;
  display: none;
  border-radius: 4px;
  animation:fromBottom 0.8s 1;
}
.qr_code:after {
  position: fixed;
  right: 51px;
  bottom: 135px;
  content: "";
  border-top: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 10px solid #e2e3e5;
  animation:fromBottom2 0.8s 1;

}
@keyframes fromBottom{
    from{
        bottom:80px;
    }
    to{
        bottom:100px;
    }
}
@keyframes fromBottom2{
    from{
        bottom:115px;
    }
    to{
        bottom:135px;
    }
}
</style>




