<template>
  <div id="note">
    <div class="imgB"></div>
    <div class="container">
      <div class="noteBox" v-for="item in items">
        <div class="title">{{item.title}}</div>
        <div class="time">{{item.date}}</div>
        <div class="brief">{{item.content}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "note",
  data() {
    return {
      items: []
    };
  },
  mounted() {
    axios("static/note.json").then(res => {
      var a = res.data.data;
      for (var i = 0; i < a.length; i++) {
        a[i].title = a[i].title;
        a[i].date = a[i].date;
        a[i].content = a[i].content;
      }
      this.items = a;
    });
  }
};
</script>


<style scoped>
.container{
    position:absolute;
    left:12.5%;
    width:75%;
    /* border:1px solid #eee; */
}
#note {
  width: 143.4%;
  height: 100%;
  margin-top: -100px;
  margin-left: -22%;
}
.imgB {
  width: 100%;
  height: 100%;
  background-image: url("../assets/hh3.png");
  overflow-x: hidden;
  background-position: center;
}
.noteBox {
  /* width: 75%; */
  /* max-width: 1220px; */
  border: 1px solid #d3d3d3;
  box-shadow: 1px 1px 10px #eee;
  /* position: absolute; */
  left: 14.5%;
  padding: 0 20px;
  margin-top: 40px;
  margin-bottom: 40px;
}
.noteBox:hover {
  box-shadow: 1px 1px 10px #d3d3d3;
}
.title {
  color: #37948a;
  text-align: left;
  /* font-size: 24px; */
  margin-top: 20px;
  margin-bottom: 10px;
}
.time {
  color: #a6acac;
}
.brief {
  color: #343434;
  margin-top: 10px;
  margin-bottom: 20px;
}
</style>


